import React from 'react';
import Slider from 'react-slick';
import ImageLoader from 'react-imageloader';
import MediaQuery from 'react-responsive';
import RaisedButton from 'material-ui/lib/raised-button';

const styleraisedbutton = {
  textAlign: 'center'
};

class Carousel extends React.Component {
  render() {
    const settings = {
      dots: false,
      infinite: true,
      speed: 3000,
      autoplaySpeed: 3000,
      autoplay: true,
      arrows: false,
      adaptiveHeight: false,
      fade: true,
      centerMode: true
    };
    return (
      <div className ="carouselscroll">
        <Slider {...settings}>
          <div className = "carousel1">
            <img className="image101" src="../resources/NewAppendLogoV2.svg"/>
            <br></br>
            <h2> Premium Small Business Apps </h2>
            <br></br>
            <RaisedButton label="Lets Talk" secondary={true} linkButton ={true} href="https://tomasrbaronas.typeform.com/to/IxD3Rq" style = {styleraisedbutton} /> 
          </div>
          <div className = "carousel2">
            <img src="../resources/WebsiteSecondPictureNewSmall.svg" className="image102"/>
          </div>
          <div className = "carousel3">
            <img src="../resources/WebsiteFirstPictureNewSmall.svg" className="image103"/>
          </div>
        </Slider>
      </div>
    );
  }
}

export default Carousel;




