import React from 'react';
import IconMenu from 'material-ui/lib/menus/icon-menu';
import IconButton from 'material-ui/lib/icon-button';
import NavigationMenuIcon from 'material-ui/lib/svg-icons/navigation/menu';
import MenuItem from 'material-ui/lib/menus/menu-item';
import Toolbar from 'material-ui/lib/toolbar/toolbar';
import ToolbarGroup from 'material-ui/lib/toolbar/toolbar-group';
import RaisedButton from 'material-ui/lib/raised-button';
import Colors from 'material-ui/lib/styles/colors';
import MediaQuery from 'react-responsive';
import ToolbarSeparator from 'material-ui/lib/toolbar/toolbar-separator';
import { Link } from 'react-router';

const style = {
  backgroundColor:'#ffffff'
};

const styleraisedbutton = {
  textAlign: 'center'
};

const iconStyles = {
  marginTop: 8
};

class TopToolbar extends React.Component {
  render() {
    return (
      <Toolbar style={style}>
        <MediaQuery query='(max-width: 950px)'>
          <ToolbarGroup firstChild={true} float="left">
            <IconMenu
              iconButtonElement={
                <IconButton touch={true}>
                  <NavigationMenuIcon color={Colors.grey500}/>
                </IconButton>
              }
            >
              <MenuItem primaryText="Home" linkButton={true} containerElement={<Link to="/" />} />
              <MenuItem primaryText="Contact Us" linkButton ={true} href="https://tomasrbaronas.typeform.com/to/IxD3Rq"/>
              <MenuItem primaryText="Blog" linkButton ={true} href="https://medium.com/@AppendSoftware" />
              <MenuItem primaryText="Login" linkButton ={true} containerElement={<Link to="/login" />} />
            </IconMenu>
          </ToolbarGroup>
        </MediaQuery>
        <MediaQuery query='(min-width: 950px)'>
          <ToolbarGroup float="right">
            <RaisedButton label="Home" secondary={true} linkButton={true} containerElement={<Link to="/" />} style = {styleraisedbutton} />
            <RaisedButton label="Contact Us" secondary={true} linkButton ={true} href="https://tomasrbaronas.typeform.com/to/IxD3Rq" style = {styleraisedbutton} />
            <RaisedButton label="Blog" secondary={true} linkButton ={true} href="https://medium.com/@AppendSoftware" style = {styleraisedbutton} />
            <RaisedButton label="Login" secondary={true} linkButton ={true} containerElement={<Link to="/login" />} style = {styleraisedbutton} />
          </ToolbarGroup>
        </MediaQuery>
      </Toolbar>
    );
  }
}

export default TopToolbar;




